import { _decorator, Component, Node, Sprite, Color, log, Prefab, tween, v3 } from 'cc';
import VDBaseScreen from '../../../../../vd-framework/ui/VDBaseScreen';
import VDScreenManager from '../../../../../vd-framework/ui/VDScreenManager';
import { ScaleFactor, scaleTo, TimeFactor } from '../../transiton/Transformation';

import { level_progress_bar } from '../../transiton/level_progress_bar';
import LocalDataManager from '../../common/LocalDataManager';
const { ccclass, property } = _decorator;

@ccclass('level1_comp')
export class level1_comp extends Component {
    @property({type: Node})
    answer: Node = null;
    @property({type: Node})
    pointer: Node = null;
    @property({type: Node})
    smile: Node = null;
    @property({type: Node})
    cry: Node = null;
    @property({type: Node})
    nextBtn: Node = null;

    @property(level_progress_bar)
    levelBarUI: level_progress_bar = null;
    // mac dinh item[0] chua dap an
    @property({type: Node})
    items: Node[] = [];

    progressLevelBar?: level_progress_bar;
    private canPlay = true;
    private level = 1;

    onClickInstruction() {
        if(this.levelBarUI){
            this.levelBarUI.handleProgress();
        }
        // this.pointer.active = true;
        // let p = this.answer.getWorldPosition();
        // this.pointer.setWorldPosition(p.x, p.y, 0);
        // let sprite =  this.answer.getComponent(Sprite);
        // sprite.color = new Color(67, 14, 180); 
        scaleTo(this.answer, ScaleFactor.BIG_BY_2, ScaleFactor.SMALL_BY_0_5, TimeFactor.MEDIUM, this.toDefault.bind(this));
    }

    onClickBtnNext(event: TouchEvent, data: any) {
        log("check number: ", data)
        
        let level2_screen = VDScreenManager.instance.assetBundle.get('res/prefabs/screen/comparison/level'+String(data), Prefab)!;
        VDScreenManager.instance.pushScreen(level2_screen, (screen: VDBaseScreen) => { }, true);
        // xoa man choi hien tai
        // VDScreenManager.instance.popScreen(true);
        // cong them kinh nghiem
        
    }

    onClickBack() {
        //cach 1
        // log(`onClickBtnPrevious`);
        VDScreenManager.instance.popScreen(true);
    }

    toDefault() {
        // let sprite =  this.answer.getComponent(Sprite);
        // sprite.color = new Color(204, 204, 204);
        // this.pointer.active = false;
    }
    start() {
        let checkDone = LocalDataManager.getObject("check", {});
        let check = checkDone.comparison[0].level1;
        this.canPlay = !check;
        log("check", check);
        if(this.canPlay){
            this.startPlay();
        }
        
        // this.canPlay = ! check;
        // let check = LocalDataManager.getObject("check", {});
        // let arr = check.comparison;
        // this.canPlay = arr[0];
    }

    startPlay() { 
        for(let i=0; i<this.items.length; i++) {
            this.items[i].on(Node.EventType.TOUCH_END, this.checkAnswer.bind(this, i));
        }
    }
    checkAnswer(i: number){
        if(this.canPlay){
            if(i==0) {
                this.processCorrect();
            }else {
                this.processWrong();
            }
        }
        
        
    }
    processCorrect(){
        let ob = LocalDataManager.getObject("check", {});
        ob.comparison[0].level1 = true;
        LocalDataManager.setObject("check", ob);
        this.canPlay = false;
        if(this.levelBarUI){
            this.levelBarUI.handleProgress();
        }
        this.smile.active = true;
        let sprite =  this.answer.getComponent(Sprite);
        sprite.color = new Color(67, 14, 180);
        this.nextBtn.active = true;
    }
    processWrong(){
        this.cry.active = true;
        tween(this.cry)
        .to(1, {scale: v3(0.08, 0.08, 0)})
        .to(0.2, {scale: v3(0, 0, 0)})
        .start();
    }
    update(deltaTime: number) {
        
    }
}


